@font-face {
    font-family: 'Sen';
    src:local('Sen'), 
        url('../fonts/Sen-Regular.woff2') format('woff2'),
        url('../fonts/Sen-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Sen';
    src:local('Sen'), 
        url('../fonts/Sen-Bold.woff2') format('woff2'),
        url('../fonts/Sen-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

